/*
 * @name: \pcs\queuingIndication.js
 * @url: http://git.evun.cn/GMES/GMES_CLIENT_PAD
 * @author: EVUN前端组
 * @modify: kelichao
 * @update: 2017-06-13
 * @decript: 上件指示
 */

var security = require("security");
var timeFormat = require("timeFormat");
var util = require("util");
var kit = require("kit");

// 模块配置
var moduleConfig = {
    configCodes: [],
    rowNum: 10,
    stationCode: "",
    title: ""
};

// 表格初始化无数据
var tempRow = {
    "productNumDetail": "--",
    "carDescription": "--",
    "remainingQuantity": "--",
    "productIdDetail": "--"
};

var queuingIndicationCtrl = {
    datatableApi: undefined,
    hasHistory: false,
    pages: undefined,// 在sweet内部已经定义,对象
    page: undefined,// 页面容器
    $page: undefined,// 页面容器（jquery对象）
    init: function () {
        // 登录时，获取模块站点信息
        security.getSysModuleConfig("PCS", "ONLINE_INDICATION").done((response) => {
            util.proConfig(response, (config) => {
                moduleConfig = config;// 获取模块站点信息，保存下来

                this.userCode = security.getUserProfile().userCode;// 获得用户信息
                this.btnEvent();
                this._getDataAndRenderScreen();// 进入页面后首次渲染
            });
        });
    },
    // 按钮事件绑定绑定
    btnEvent: function () {
        
        var _this = this;
        $("#queueIndRender").on("click", ".confirmBtn", function () {

            util.service("GMES-CLIENT-PCS0003", {
                "stationCode": moduleConfig["stationCode"],
                "productNum": $(".productNum").attr("productnum"),
                "productTrackNum": "",
                "productionSeqNum": $(".productId").attr("productid"),
                "createUser": _this.userCode,
            }, "正在确认")
                .done(function (response) {
                    if (response.returnCode === "0") {
                        notie.alert({type: "1",text: "产品分装上线记录成功"});
                        _this._getDataAndRenderScreen();
                    } else {
                        notie.alert({text: "产品分装上线记录失败"});
                    }
                });
        });

        // 历史记录按钮绑定事件
        $(".more-history").click(function () {
            _this.getHistory();
        });

        util.enterEvent(function() {
            $(".confirmBtn").trigger("click");
        });
    },
    // 获取数据并且渲染屏幕
    _getDataAndRenderScreen: function () {

        util.service("GMES-CLIENT-PCS0011", {
            stationCode: moduleConfig.stationCode,
            rowNum: moduleConfig.rowNum || 0,
            num: moduleConfig.rowNum || 0,
            pageNum: 0,// 当前页码
            getNum: 3,// 每页数量
            stNum: 2,// 新增参数 初始位置
        }, "正在加载中")
            .done(function (response) {
                var data = null;// 主数据容器

                if (response.returnCode !== "0") {
                    return;
                }

                var arr = response.data.productionQueues;
                if (arr.length === 0) {
                    notie.alert({type: "1",text: "当前站点无数据"});
                    // return;
                }

                kit.forEach(arr, function(value, index) {
                    arr[index] = util.processData(moduleConfig, arr[index]);
                });

                data = arr[0] || {};
                data.productionQueues = [(arr[1] || tempRow), (arr[2] || tempRow)];

                var thresholdValue = moduleConfig.thresholdValue || 5;
                if (parseInt(data.remainingQuantity) <= parseInt(thresholdValue)) {
                    data.warnShineFlag = true;
                }

                util.tplManager("queueIndTpl", data, "queueIndRender");
            });
    },
    sure: function () {
        //回车键触发上线确认操作
        util.hotkeysBind("keydown", "return", function () {
            $(".confirmBtn", this.$page).click();
        });
    },
    setPopup: function() {
        util.setPublicPopup("上件指示历史记录",
        `<div class="table-panel">
           <table class="table-bordered table-striped w100p">
               <thead>
                   <th>产品顺序</th>
                   <th>站点编号</th>
                   <th>生产管理号</th>
                   <th>产品跟踪号</th>
                   <th>物料描述</th>
                   <th>创建时间</th>
               </thead>
           </table>
       </div>`);
    },
    getHistory: function () {
        this.setPopup();
        this.datatableApi = util.dataTable($(".popup-public .table-panel table"), {
            ordering: false,
            dom: "<'row'<'col-50'f>> <'row'<'col-100'tr>> <'row'<'col-50'p>>",// 控制Datatables元素的位置(dom)
            scrollX: true,
            scrollY: true,
            destroy: true,
            pageLength: 5,
            order: [],
            ajax: {
                serviceCode: "GMES-CLIENT-PCS0007",
                param: () => {
                    return {
                        serviceCode: "GMES-CLIENT-PCS0007",
                        siteCode: app.siteCode,
                        stationCode: moduleConfig["stationCode"],
                        userCode: this.userCode,
                        languageCode: "zh",
                        pageNum: 1,
                        productNum: "",
                        productTrackNum: "",
                        createDateTimeSt: "",
                        createDateTimeEd: "",
                    };
                }
            },
            createdRow: (row, data) => {
                if (data.statusEcode != 1) {
                    $(row).addClass("error");
                }
            },
            columns: [
                {
                    data: "id",
                    render: (data, type, row, meta) => {
                        return meta["settings"]["_iDisplayStart"] + meta.row + 1;
                    }
                },
                {
                    data: "stationCode",
                    name: "stationCode"
                },
                {
                    data: "productionSeqNum",
                    name: "productionSeqNum"
                },
                {
                    data: "productNum",
                    name: "productNum"
                },
                {
                    data: "materialText",
                    name: "materialText"
                },
                {
                    data: "createDateTime",
                    name: "createDateTime",
                    render: (data) => {
                        this.hasHistory = true;
                        return timeFormat.three(data);
                    }
                }
            ]
        });
    }
};

module.exports = queuingIndicationCtrl;


